const validasiMutasiCiok = (value) => {
  const errors = {};
  if (!value.bruto) {
    errors.bruto = "Tidak Boleh kosong";
  }
  if (!value.netto) {
    errors.netto = "Tidak Boleh kosong";
  }
  if (!value.noBon) {
    errors.noBon = "Tidak Boleh kosong";
  }
  if (!value.noBon) {
    errors.noBon = "Tidak Boleh kosong";
  }
  if (!value.transaksi) {
    errors.transaksi = "Tidak Boleh kosong";
  }
  if (!value.tanggal) {
    errors.tanggal = "Tidak Boleh kosong";
  }
  return errors;
};

export default validasiMutasiCiok;
